<template>
<div @click="onClick">
  <table class="ui red selectable striped attached fixed table">
    <thead>
      <tr>
        <th class="center aligned one wide">#</th>
        <th class="ten wide">Answer</th>
        <th class="two wide">Date</th>
        <th class="three wide">Answerer</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="(answer, index) in rowData.answers">
        <td class="center aligned">{{index + 1}}</td>
        <td>
          <vue-markdown :source="answer.html ? answer.html : answer.text"></vue-markdown>
        </td>
        <td>{{answer.createdOn|formatDate}}</td>
        <td>{{answer.displayName}}</td>
      </tr>
    </tbody>
  </table>
  <div class="ui teal segment">
    <add-question :space="rowData._room" :question="rowData" button-class="primary">
    </add-question>
  </div>
</div>
</template>


<script>
import addQuestion from './addQuestion.vue'
import VueMarkdown from 'vue-markdown'

export default {
  components: {
    VueMarkdown
  },
  props: {
    rowData: {
      type: Object,
      required: true
    },
    rowIndex: {
      type: Number
    }
  },
  methods: {
    onClick(event) {
      console.log('my-detail-row: on-click', event.target)
    }
  },
}
</script>

<style scoped>
td {
  word-wrap: break-word;
}
</style>



